"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";

import { loginSchema } from "../../../schemas/loginSchema.ts"; //√© o objeto com schema do zod na execu√ß√£o
import type { LoginSchema } from "../../../schemas/loginSchema.ts"; //tipo usado pelo ts para checar os erros em desenvolvimento



import { users, type User } from "@/data/users.ts";

import { useState } from "react";

import { useRouter } from "next/navigation";

import Link from "next/link";

import "./../../globals.css";
import { SiLaravel } from "react-icons/si";




import {
  Form,
  FormField,
  FormItem,
  FormLabel,
  FormControl,
  FormMessage,
} from "@/components/ui/form";

import { Input } from "@/components/ui/input";

import { Button } from "@/components/ui/button";

import { Card, CardTitle, CardDescription, CardHeader, CardContent, CardFooter } from "@/components/ui/card.tsx";



const Home = () => {
  
  const [loginError, setLoginError] = useState<string>("");

  // Hook do Next.js para navega√ß√£o
  const router = useRouter();

  // Inicializa√ß√£o do formul√°rio
  const form = useForm<LoginSchema>({
    resolver: zodResolver(loginSchema),
    defaultValues: {
      email: "",
      password: "",
    },
  });

  // Fun√ß√£o de envio do formul√°rio
  function onSubmit(values: LoginSchema) {
    // LOGIN LOCAL PARA TESTE
    const user: User | undefined = users.find(
      (user) =>
        user.email === values.email && user.password === values.password
    );

    if (user) {
      // Redireciona o usu√°rio para o dashboard
      router.push("/dashboard");
      console.log("Dados enviados:", values);
    } else {
      setLoginError("Email ou senha inv√°lidos");
    }
  }

  return (
    <>
   
    <div className="h-screen flex items-center justify-center p-4 bg-gradient-to-bl from-slate-900 via-slate-800 to-slate-900">
      <div className="flex flex-col items-center w-full max-w-[460px] space-y-4">
        {/* √çcone em c√≠rculo */}
        <div className="w-12 h-12 flex items-center justify-center rounded-full bg-blue-500 border-white">
          <SiLaravel className="w-8 h-8 text-white" />
        </div>

        {/* Texto abaixo do √≠cone */}
        <span className="text-lg md:text-xl font-semibold text-gray-100">
          Via Vendas
        </span>

          <Card className=" w-full 
                            h-full 
                            md:max-w-lg 
                            lg:max-w-xl
                            border border-gray-600 shadow-xl bg-gray-800 mt-06 p-2 mt-4">
            <CardHeader className="space-y-2 text-center">
              <CardTitle className="font-bold text-xl md:text-2xl lg:texto-3xl tracking-tigher text-gray-100 mt-2">Bem vindo de volta</CardTitle>
              <CardDescription className=" text-sm md:text-base l\g:text-lg text-gray-400">Informe seu Email e senha para entrar</CardDescription>
            </CardHeader>

            <CardContent>
            
              <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 "> {/* form.handleSubmit - valida os dados, se estiver ok, chama a fun√ß√£o onSubmit */}
                  <FormField
                    control={form.control}
                    name="email"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel className="text-gray-100">E-mail</FormLabel>
                        <div className="relative">
                          <FormControl className="w-full rounded-md border-gray-400 p-3 br-gray-600">                          
                            <Input placeholder="exemplo@email.com" {...field} className="text-gray-400 bg-gray-600 border border-gray-500 "/>                          
                          </FormControl >
                          <FormMessage className="absolute text-sx text-red-500 mt-1 left-1 top-full"/>
                        </div>

                     </FormItem>
                    )}
                    />

                  <FormField
                    control={form.control}
                    name="password"
                    render={({ field }) => (
                      <FormItem>
                        <div className="flex flex-col md:flex-row md:items-center md:justify-between">
                        <FormLabel className="text-gray-100 mt-4 md:mt-6" >Senha</FormLabel>
                        <Link href="/forgotPassword" className="text-sm text-blue-400 font-semibold mt-2 md:mt-4 md:ml-4 
                                                                hover:underline underline-offset-4"> Esqueceu sua senha?</Link>
                        </div>
                        <div className="relative">
                          <FormControl className="w-full rounded-md border-gray-400 p-3 bg-gray-600">                
                            <Input type="password" placeholder="******" {...field} className="w-full text-gray-400 bg-gray-600 border border-gray-500" />                          
                          </FormControl>
                          <FormMessage className="absolute text-sx text-red-500 mt-1 left-1 top-full" />
                        </div>
                      </FormItem>
                    )}
                    />

                    {/* MENSAGEM DE ERRO QUANDO LOGIN E SENHA NAO EXISTEM */}

                  <span>{loginError && (<p className="text-red-500 text-sm text-center">{loginError}</p>)}</span>

                  <FormLabel className="flex items-center space-x-2">
                          {/* Cont√™iner do checkbox */}
                          <div className="relative w-4 h-4">
                            {/* Checkbox real (invis√≠vel mas funcional) */}
                            <Input
                              type="checkbox"
                              className="peer absolute inset-0 w-4 h-4 opacity-0"
                            />

                            {/* Caixa customizada */}
                            <div
                              className="w-4 h-4 border border-gray-500 rounded
                                        flex items-center justify-center
                                        peer-checked:bg-gray-100 peer-checked:border-gray-100 mt-2"
                            ></div>

                            {/* Check animado */}
                            <span
                              className="
                                absolute text-black text-[16px] left-0.5 top-[7px] 
                                opacity-0 scale-50 pointer-events-none
                                transition-all duration-200 ease-out
                                peer-checked:opacity-100 peer-checked:scale-100">‚úì</span>
                          </div>

                          {/* Texto ao lado */}
                          <span className="text-xs md:text-sm text-gray-100 mt-3">Lembrar-me</span>
                        </FormLabel>

                  <Button type="submit" className="w-full bg-blue-500 text-white 
                                      hover:bg-blue-600 
                                      focus:outline-none 
                                      focus:ring-2 focus:ring-blue-400 focus:ring-offset-2 focus:ring-offset-gray-900
                                      mt-6 h-10
                                      transition-transform duration-200">Entrar</Button>
                </form>
              </Form>
            </CardContent>
            <CardFooter className="flex flex-col sm:flex-row flex-wrap items-center justify-center gap-1 mt-4 text-center">
                <span className="text-sm text-gray-400 whitespace-normal wrap-break-words">Nao tem uma conta?</span>
                <Link href="/register" className="text-sm font-semibold text-blue-400 hover:underline underline-offset-4 wrap-break-words">Cadastrar</Link>
            </CardFooter>
          </Card>   
      </div>
    </div>
    </>
  )
}

export default Home
